<template lang="html">
    <section class="todoapp">
		<header class="header">
			<h1>todos</h1>
			<input type="text" placeholder="what need to be done?" class="new-todo" @keyup.enter="add($event)">
		</header>
		<section class="main" style="">
			<input type="checkbox" class="toggle-all" :checked="checkAll" @change="changestate(!checkAll)">
				<ul class="todo-list" v-for="(todo,index) in filterTodos">
                    <todos :todo="todo" :index="index"></todos>
				</ul>
		</section>
		<footer class="footer" style="">
			<span class="todo-count"><strong>{{remaining}}</strong> items left</span>
			<ul class="filters">
				<li><a href="" :class="{selected: visibility=='all'}" @click.prevent="visibility='all'">all</a></li>
				<li><a href="" :class="{selected: visibility=='active'}" @click.prevent="visibility='active'">active</a></li>
				<li><a href="" :class="{selected: visibility=='completed'}" @click.prevent="visibility='completed'">completed</a></li>
			</ul>
			<button class="clear-completed" v-show="total" @click="clear">clear completed</button>
		</footer>
	</section>
</template>

<script>
import {mapState,mapGetters,mapMutations} from 'vuex'
import Todos from './components/Todos'

const filter = {
	all: todos=>todos,
	active: todos=> todos.filter(todos=>!todos.done),
	completed: todos=> todos.filter(todos=>todos.done)  
}
export default {
	data(){
		return {
			visibility: 'all'
		}
	},
    computed: {
        ...mapState(['todos']),
        ...mapGetters(['checkAll','remaining','total']),
        filterTodos(){
        	return filter[this.visibility](this.todos);
        }
    },
    components: {
    	Todos
    },
    methods: {
    	add(e){
    		let name = e.target.value;
    		if(name){
                this.$store.commit('add',{name});
                e.target.value = '';
    		}
    	},
    	changestate(status){
            this.$store.commit('change',status);
    	},
    	clear(){
    		this.$store.commit('clear');
    	}
    }
}
</script>

<style lang="css">
</style>



