<template>
    <li class="todo" :class="{completed: todo.done,editing: editing}">
		<div class="view">
			<input type="checkbox" class="toggle" :checked="todo.done" >
			<label @dblclick="editing = true">{{todo.name}}</label>
			<button class="destroy" @click="del(index)"></button>
		</div>
		<input type="text" class="edit" :value="todo.name" @keyup.enter="doneEdit($event)" autofocus>
	</li>
</template>

<script>
export default {
	data(){
		return {
			editing: false
		}
	},
    props: {
    	todo: {
    		type: Object,
    		required: true
    	},
    	index: {
    		type: Number,
    		required: true
    	}
    },
    methods: {
    	del(index){
            this.$store.commit('del',index);
    	},
    	toggleState(index){
            this.$store.commit('toggle',index)
    	},
    	doneEdit(e){
            //获取文本框的值
            let name = e.target.value;
            //判断text是否有值
            if (name) {
                //更新操作
                this.$store.commit('update',{name,index: this.index});
            } else {
                this.del(this.index);
            }
            this.editing = false;
        },
    }
}
</script>

<style>
	
</style>


