export default {
	add(state,{name}){
        state.todos.push({name,done: false})
	},
	del(state,index){
        state.todos.splice(index,1)
	},
	toggle(state,index){
        state.todos[index].done = !state.todos[index].done;
	},
	change(state,done){
        state.todos.forEach((item,index)=>{
            item.done = done;
        })
	},
	update(state,{name, index}){
        state.todos[index].name = name;
    },
	clear(state){
        state.todos = state.todos.filter(todos=>!todos.done);
	}
}


