export default {
	checkAll(state){
        return state.todos.every(todos=>todos.done);
	},
	// 统计未完成的任务
	remaining(state){
        return state.todos.filter(todos=>!todos.done).length;
	},
	total(state){
        return state.todos.some(todos=>todos.done)
	}
}


