// 引入vue和vuex
import Vue from 'vue'
import Vuex  from 'vuex'

Vue.use(Vuex)

// 引入getters、mutations、actions
import getters from './getters'
import mutations from './mutations'
import actions from './actions'

// 定义state
const state = {
	todos: [
        {name: '吃饭',done: true},
        {name: '学习',done: false},
        {name: '睡觉',done: false}
    ]
}

// 实例化Stote对象
export default new Vuex.Store({
	state,
    getters,
    mutations,
    actions
}) 


